import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import CreateNotification from '../components/CreateNotification';
import ArchiveNotification from '../components/ArchiveNotification';
import { Route, Switch } from 'react-router-dom';
import RequireAuth from '../components/auth/require_auth';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
const styles = {
  root: {
    flexGrow: 1,
  },
};

class Events extends React.Component {
  componentDidMount() {
    if (this.props.selected !== 3) this.props.update();
  }
  render() {
    const { history } = this.props;
    return(
      <div>
        <Tabs style={{marginBottom: 20}} value={history.location.pathname}>
          <Tab
            value='/notifications'
            label='Notify your audience'
            onClick={() => history.push('/notifications')}/>
          <Tab
            value='/notifications/archive'
            label='Archive'
            onClick={() => history.push('/notifications/archive')}
          />
        </Tabs>
        <Switch>
          <Route exact path="/notifications" component={RequireAuth(CreateNotification)} />
          <Route exact path="/notifications/archive" component={RequireAuth(ArchiveNotification)} />
        </Switch>
      </div>
    );
  }
}
Events.propTypes = {
  classes: PropTypes.object.isRequired,
  selected: PropTypes.number.isRequired,
  update: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired
};

const mapStateToProps = (state) => {
  return { selected: state.status.selected };
};
const mapDispatchToProps = (dispatch) => {
  return {
    update: () => {
      dispatch({
        type: 'UPDATE_ROUTE',
        payload: 3
      });
    }
  };
};
export default connect(mapStateToProps, mapDispatchToProps) (withStyles(styles) (Events));


