import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import PropTypes from 'prop-types';
import Typography from '@material-ui/core/Typography';
import { connect } from 'react-redux';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListSubheader from '@material-ui/core/ListSubheader';
import axios from 'axios';
import { UPDATE_BUNDLE } from '../actions/types';
import { Card, CardContent, Divider, Chip } from '@material-ui/core';
const styles = theme => ({
  root: {
    flexGrow: 1
  },
  chip: {
    margin: theme.spacing.unit,
  },
  root_list: {
    flexGrow: 1,
    overflow: 'auto',
    height: 350
  },
  paper: {
    textAlign: 'center',
    height: 380,
    color: theme.palette.text.secondary,
  },
  paper_list: {
    textAlign: 'center',
    paddingTop: theme.spacing.unit,
    color: theme.palette.text.secondary,
  },
  listSection: {
    backgroundColor: 'inherit',
  },
  ul: { 
    backgroundColor: 'inherit',
    padding: 0,
  },
  date: {
    backgroundColor: '#fff'
  }
});
class ArchiveNotification extends React.Component {
  state ={
    currentNotification: { parent: '', currentElement: { reach: [], name: 'Please select an event', audience: [], media: [], id: '', description: '' } },
    draft: JSON.parse(sessionStorage.getItem('archive_notification_data')) ? JSON.parse(sessionStorage.getItem('archive_notification_data')) : [],
  }
  loadEvent = (number1, number2) => {
    let toLoad = this.state.draft[number1].data[number2];
    console.log(toLoad);
    this.setState({ currentNotification: { parent: number1, currentElement: {...toLoad, audience: toLoad.audience.split(',') } }, description: toLoad.description });
  }
  componentDidMount() {
    axios.post('/web/bundle-check', { hashsum: this.props.auth.bundle.hashsum ,email: this.props.auth.user_email }, { headers: {'x-access-token': this.props.auth.user_token } })
      .then(response => {
        if(!response.data.error){
          if(response.data.bundle) {
            this.props.updateBundle({
              bundle: response.data.bundle
            });
          }
        }
      })
      .then( () => {
        axios.post('/web/notification-data-from-list', { notification_list: this.props.auth.bundle.bundle.notifications.slice((this.props.auth.bundle.bundle.notifications.length - 100), this.props.auth.bundle.bundle.notifications.length) ,email: this.props.auth.user_email }, { headers: {'x-access-token': this.props.auth.user_token } })
          .then(response => {
            if(!response.data.error){
              if(response.data.data) {
                if (this.unmounted) return;
                this.setState({ draft: response.data.data });
              }
            }
          });
      });

  }
  componentWillUnmount() {
    sessionStorage.setItem('archive_notification_data',JSON.stringify(this.state.draft));
    this.unmounted = true;
  }
  handleChangeDescription() {    
    this.setState({
      description: this.state.currentNotification.currentElement.description
    });
  }
  render() {
    const { classes } = this.props;
    const { draft } = this.state;
    return(
      <div className={classes.root}>
        <Grid container spacing={24}>
          <Grid item xs={12} sm={4} >
          
            <Paper className={classes.paper_list}>
              <Typography variant="title" align="center">
                Notifications
              </Typography>
              <div className={classes.root_list} >
                <List className={classes.root} subheader={<li />}>
                  {draft && draft.map((currentRange, number1) => (
                    <li key={`section-${currentRange.date}-${number1}`} className={classes.listSection}>
                      <ul className={classes.ul}>
                        <ListSubheader className={classes.date}>{`Created on ${new Date(currentRange.date).getDate()}-${new Date(currentRange.date).getMonth()}-${new Date(currentRange.date).getFullYear()}`}</ListSubheader>
                        {currentRange.data.map((item, number2) => (
                          <ListItem button onClick={() => this.loadEvent(number1, number2)} key={`item-${item.name}-${item}-${number2}`}>
                            <ListItemText primary={`${item.name}`} />
                          </ListItem>
                        ))}
                      </ul>
                    </li>
                  ))}
                  {
                    !this.state.loading &&  draft && draft.length === 0 && 
                    <ListItem divider={true} button>
                      <ListItemText primary={'No Notifications Created Yet'} secondary={'Your Notification history can be accessed here'} />
                    </ListItem>
                  }
                </List>
              </div>
              {
                !this.state.loadingData &&  draft && draft.length === 0 && <Typography style={{margin: 10}} variant='caption'> You have not created any notifications yet, to get started check out the create tab </Typography>
              }
            </Paper>
          </Grid>
          <Grid item xs={12} sm={8}>
            { this.state.currentNotification.currentElement.id !== '' ? <Card className={classes.card}>
              <CardContent>
                <Typography variant="body2" gutterBottom>
                  {this.state.currentNotification.currentElement.description}
                </Typography>
                {this.state.currentNotification.currentElement.audience.map( (element)=> 
                  <Chip key={ `chip ${element}` } label={element} className={classes.chip} />
                )}
                <Divider/>
                <Typography variant="caption">
                  Reached {this.state.currentNotification.currentElement.reach.length} users
                </Typography>
              </CardContent>
            </Card> : <Typography>Please select a notification to view its details</Typography>}
          </Grid>
          
        </Grid>
      </div>
    );
  }
}
ArchiveNotification.propTypes = {
  classes: PropTypes.object.isRequired,
  auth: PropTypes.object.isRequired,
  updateBundle: PropTypes.func.isRequired
};

const mapStateToProps = (state) => {
  return { 
    auth: state.auth
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    updateBundle: (bundle) => {
      dispatch({
        type: UPDATE_BUNDLE,
        payload: bundle
      });
    }
  };
};
export default connect(mapStateToProps, mapDispatchToProps) (withStyles(styles) (ArchiveNotification));


