import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import axios from 'axios';
import Snackbar from '@material-ui/core/Snackbar';
import green from '@material-ui/core/colors/green';
import classNames from 'classnames';
import CircularProgress from '@material-ui/core/CircularProgress';
import { connect } from 'react-redux';
import Tree from './Tree';
import { TextField } from '@material-ui/core';

const styles = (theme) => ({
  root: {
    padding: 20
  },
  myHolder: {
    padding: 5,
    margin: 5
  },
  customHelper: {
    color: 'rgba(0, 0, 0, 0.54)',
    margin: '0',
    fontSize: '0.75rem',
    textAlign: 'left',
    marginTop: '8px',
    minHeight: '1em',
    fontFamily: '"Roboto", "Helvetica", "Arial","sans-serif" ',
    lineHeight: '1em',
    display: 'block',
    marginBefore: '1em',
    marginAfter: '1em',
    marginStart: '0px',
    marginEnd: '0px',
  },
  wrapper: {
    margin: theme.spacing.unit,
    position: 'relative',
  },
  buttonSuccess: {
    backgroundColor: green[500],
    '&:hover': {
      backgroundColor: green[700],
    },
  },
  buttonProgress: {
    color: green[500],
    position: 'absolute',
    top: '50%',
    left: '50%',
    marginTop: -12,
    marginLeft: -12,
  },
});
class CreateNotification extends React.Component {
  state = {
    draft: { description: '' },
    submitting: false,
    open: false,
    success: false,
    mssg: ''
  };
  timer = undefined;
  componentWillUnmount() {
    clearTimeout(this.timer);
  }

  handleSubmit = () => {
    if(this.state.draft.description.length === 0) 
      return this.setState({open: true, mssg: 'No description provided'});
    const descriptionErr = this.state.draft.description.length < 8 || this.state.draft.description.length >= 800 ? true : false;
    if(descriptionErr) 
      return this.setState({open: true, mssg: 'please provide a valid description'});
    let audienceRaw = sessionStorage.getItem('draft_audience');
    if(!audienceRaw) 
      return this.setState({open: true, mssg: 'please select an audience'});    
    let audience = JSON.parse(audienceRaw).audience;
    let audienceData = [];
    for (const key of Object.keys(audience)) {
      audienceData.push(audience[key].label);
    }
    if( audienceData.length === 0 ) 
      return this.setState({open: true, mssg: 'please select an audience'});
    var formData = new FormData();
    formData.append('description', this.state.draft.description);
    formData.append('email', this.props.auth.user_email);
    formData.append('audience', audienceData);
    this.setState( { submitting: true, success: false } );
    axios.post('/notifications/create-notification', formData, {
      headers: {
        'Content-Type': 'multipart/form-data',
        'x-access-token': this.props.auth.user_token
      }
    }).then( response => {
      if(!response.data.error) {
        sessionStorage.setItem('draft_audience', JSON.stringify( { audience: [] }) );
        this.setState({draft: { description: '' }, success: true, submitting: false, open: true, mssg: 'Notification was successfully sent'}
          ,() => {
            this.timer = setTimeout(() => {
              this.setState({
                loading: false,
                success: false,
              });
            }, 2000);
          },
        );
      } else {
        this.setState({submitting: false, success: false, open: true, mssg: 'An error has occured, please try again later'});
      }
    })
      .catch( err => {
        if(err.response.status === 500) {
          window.location.reload();
        }
        this.setState({submitting: false, success: false, open: true, mssg: 'An error has occured, please try again later'});
      });

  }
  handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    this.setState({ open: false });
  };

  handleChange = prop => event => {
    this.setState({ draft: { ...this.state.draft, [prop]: event.target.value } });
  };


  render() {
    const { classes } = this.props;    
    const descriptionErr = this.state.draft.description.length < 8 || this.state.draft.description.length >= 800 ? true : false;
    const submitting = this.state.submitting;
    const success = this.state.success;
    const buttonClassname = classNames({
      [classes.buttonSuccess]: success,
    });
    const loading = submitting;
    return (
      <Paper>
        <div className={classes.root}> 
          <Grid container> 
            <Grid item xs={12} className={classes.myHolder}>
              <Typography variant="headline" style={{marginBottom:5 }}>Notification</Typography>
              <TextField
                id="textarea"
                label="Enter content here"
                placeholder="You can begin typing"
                multiline
                fullWidth
                rows="4"
                rowsMax="15"
                inputProps={{
                  maxLength: 800,
                }}
                value={this.state.draft.description}
                className={classes.textField}
                onChange={this.handleChange('description')}
                margin="normal"
              />
              <Typography className={classes.customHelper} style={{color: descriptionErr && this.state.draft.description.length !== 0 ? '#FF5252':'', fontWeight: descriptionErr && this.state.draft.description.length !== 0 ? 'bold':''}}>Description must be between 8 to 800 characters </Typography>
            </Grid> 

            <Grid item xs={12} className={classes.myHolder} >
              <Typography variant="headline">Select Audience</Typography>
              <Tree hierarchy={this.props.auth.bundle.bundle.hierarchy ? this.props.auth.bundle.bundle.hierarchy : []} />
            </Grid> 
            
            <Grid item xs={12} className={classes.myHolder} >
              <Snackbar
                anchorOrigin={{
                  vertical: 'bottom',
                  horizontal: 'left',
                }}
                open={this.state.open}
                autoHideDuration={2000}
                onClose={this.handleClose}
                message={<span id="message-id">{this.state.mssg}</span>}                
              />
            </Grid> 

            <div className={classes.wrapper}>
              <Button
                variant="raised"
                color="primary"
                className={buttonClassname}
                disabled={loading}
                onClick={this.handleSubmit}
              >
                  Send notification
              </Button>
              {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
            </div>
          </Grid>
        </div>
      </Paper>
    );
  }
}

CreateNotification.propTypes = {
  classes: PropTypes.object,
  auth: PropTypes.object.isRequired
};
const mapStateToProps = (state) => {
  return { 
    auth: state.auth
  };
};

export default connect(mapStateToProps)(withStyles(styles)(CreateNotification));



