import React from 'react';
import DropdownTreeSelect from 'react-dropdown-tree-select';
import 'react-dropdown-tree-select/dist/styles.css';
import '../css/Tree.css';

class Tree extends React.Component {
  constructor(props) {
    super(props);
    this.getTreeData = this.getTreeData.bind(this);
  }
  componentWillMount() {
    const local_draft = sessionStorage.getItem('draft_audience');
    if(!local_draft)
      sessionStorage.setItem('draft_audience', []);
  }
  getTreeData() {
    let current = localStorage.getItem('hierarchy') ;
    if(current){
      try {
        current = JSON.parse(atob(current)) ? JSON.parse(atob(current)) : null ;
      } catch (e){
        return [];
      }
    }
    if(!current || current.constructor !== Array) {
      localStorage.removeItem('hierarchy');
      localStorage.removeItem('token');
      localStorage.removeItem('token-data');
      window.location.reload();
    }
    const local_draft = sessionStorage.getItem('draft_audience');
    if(local_draft && JSON.parse(local_draft) && JSON.parse(local_draft).audience && (current.constructor === Array) && current && current[0] ) {
      let selectedNodes;
      try {
        selectedNodes = JSON.parse(local_draft).audience;
      } catch(e) {
        return current;
      }
      let dope = current[0];
      if(dope.checked === undefined) {
        dope.checked = false;
      }
      
      if(dope.children && dope.children.length > 0) {
        let j;
        let queue = [dope.children];
        while (queue.length > 0) {
          let currentQueueElement = queue[0];
          for(j=0;j<currentQueueElement.length;j++) {
            currentQueueElement[j].checked = false;
            if(currentQueueElement[j].children) {
              queue.push(currentQueueElement[j].children);
            }
          }
          queue.splice(0,1);
        }
      }
      for (const key of Object.keys(selectedNodes)) {
        let path = selectedNodes[key]._id.split('-');
        let dope = current[path[0]];
        let i;
        for(i=1;i<path.length;i++) {
          dope = dope.children[parseInt(path[i], 10)];
        }
        dope.checked = true;

        if(dope.children && dope.children.length > 0) {
          let j;
          let queue = [dope.children];
          while (queue.length > 0) {
            let currentQueueElement = queue[0];
            for(j=0;j<currentQueueElement.length;j++) {
              currentQueueElement[j].checked = true;
              if(currentQueueElement[j].children) {
                queue.push(currentQueueElement[j].children);
              }
            }
            queue.splice(0,1);
          }
        }
      }
    } 
    return current;
  }

  onTreeChange(currentNode, selectedNodes) { 
    sessionStorage.setItem('draft_audience',JSON.stringify({ audience: selectedNodes }));
  }

  render() {
    return(
      <DropdownTreeSelect clearSearchOnChange={true} keepTreeOnSearch={true} onChange={this.onTreeChange.bind(this)} showPartiallySelected={true} data={this.getTreeData()} className="mdl-demo"/>
    );
  }
}

export default (Tree);


